import React, {useState} from 'react'
import styled from 'styled-components'
import ScrollToTop from '../../components/Signin/ScrollToTop'
import Footer from '../../components/Footer/Footer'
import BreadCrumbs from '../components/Breadcrumbs'
import NavbarBottom from '../components/NavbarBottom'
import PubLaterale from '../components/Publicite'
import Sidebar from '../components/Publicite/Sidebar'
import { Container, Grid,makeStyles } from '@material-ui/core'
import {dataActualite} from '../components/data/dataActualite'
import SearchBar from '../components/SearchBar/SearchBar'
import CardFull from '../components/Cards/CardFull'





const HeadSection = styled.div`
   position:fixed;
   width:100%;
   z-index:10;
   background-color:#fff;
   box-shadow: 0 6px 20px rgba(10, 10, 10, 0.1);

`

const PageContent = styled.div`   
   padding-top: 185px;
   margin-left:70px;
   margin-bottom: 20px;

   @media screen and (max-width: 768px){
       margin-left:30px;
    }
  
`

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 2,
     
    }
  }));


const Actualites = () => {

    const classes = useStyles();

    const [isOpen, setIsOpen] = useState(false)

    const toggle = () =>{
        setIsOpen(!isOpen)
    }
    return (
        <>
            <ScrollToTop/>
            <HeadSection>
                <Sidebar  isOpen={isOpen}  toggle={toggle}/>
                <PubLaterale   toggle={toggle}/>
                <NavbarBottom/>
                <BreadCrumbs/>
            </HeadSection>

            <PageContent className={classes.root}>
                    <Container>

                           
                            <Grid container spacing={3} lg={12} md={12}  xs={12}>
                                <Grid xs={12}>
                                    <SearchBar/>
                                </Grid>
                               {dataActualite.map(actualite => 
                                 <Grid item lg={4} md={6}  xs={12}  key={actualite.id}>
                                      <CardFull   {...actualite}  />
                                 </Grid>
                              )}
                            </Grid>

                    </Container>
            </PageContent>
            <Footer/>  
        </>
    )
}

export default Actualites
