const useStyles = makeStyles((theme) => ({
   
    button: {
        margin: theme.spacing(1),
        backgroundColor:"#fbf000",
        color:'#1a1359',
        borderRadius:"none",
        textTransform: 'none',
        "&:hover":{
            backgroundColor: "#1a1359",
            color:'#fff',
        }
      
      },
      textField:{
        height:'35px',
        backgroundColor: "#FFF",
        justifyContent:'center',
        alignItems:'center',
      }
   
  }));

const SearcHeader = (props) => {

    const classes = useStyles();
    const [loading, setLoading] = useState(false);
    const [jobSearch, setJobSearch] = useState({
        ville_offre : "",
        secteur_activite: ""
    })

    const handleChange = e => {
        e.persist();
        setJobSearch((oldState) => ({
           ...oldState,
           [e.target.name] : e.target.value,
        })) ;
       
    }

    const search =  async () => {
        setLoading(true);
        await props.fetchJobsCustom(jobSearch);
        setLoading(false);

    }
    return (
        <div> 
            
                   <SearchMenu>
                       <SearchItem>
                             <Span>
                                 <LocationOnIcon/>
                             </Span>
                             <Select  
                                style={{minWidth: 210,backgroundColor: '#fff'}} 
                                name="ville_offre"
                                disableUnderline
                                variant="standard"
                                value={jobSearch.ville_offre}
                                onChange={handleChange}
                             
                             >
                               {
                                    getVilleCollection().map(
                                        item => (<MenuItem key={item.id} value={item.title.toLocaleLowerCase()}>{item.title}</MenuItem>)
                                    ) 
                                }
                             </Select>
                              {/* <Controls.Select
                                  label="Ville"
                                  options={getVilleCollection()}
                                  value={jobSearch.ville_offre}
                                  onChange={handleInputChange}
                              />*/}
                       </SearchItem>
                       <SearchItem>
                             <Span>
                                 <SearchIcon/>
                             </Span>
                             <TextField
                                variant="outlined"
                                label="(ex:informatique)"
                                name="secteur_activite"
                                value={jobSearch.secteur_activite.toLocaleLowerCase()}
                                size="small"
                                className={classes.textField}
                                disableUnderline
                                onChange={handleChange}
                                
                             >

                             </TextField>
                              {/*<Controls.Input
                                    label="(ex:commerce)"
                                    size="small"
                                    value={jobSearch.secteur_activite}
                                    onChange={handleInputChange}
                              />*/}
                       </SearchItem>
                       <Button
                        variant="standard"
                        className={classes.button}
                        
                        disabled={loading}
                        onClick={search}
                       >
                           {loading ? (
                                    <CircularProgress color="secondary" size="12"/>

                              ) : (
                                    "Rechercher"
                              )

                           }
                          
                       </Button>
                            {/* <Controls.Btn
                                   type="submit"
                                   text="Rechercher"
                                   variant="outlined"

                                 />*/}
                     
                   </SearchMenu>
            
           
        </div>
    )
}

export default SearcHeader
